<template>
  <div class="login-wrap">
      <el-card class="box-card">
          <!-- <div class="user-headimg">

          </div> -->
        <el-form class="el-form-wrap" ref="formRef" :rules="loginRuules" :model="form" label-width="0px">
            <el-form-item label="" prop="name">
                <el-input clearable prefix-icon="el-icon-user-solid" placeholder="用户名" v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item class="password-item" label="" prop="password">
                <el-input show-password clearable prefix-icon="el-icon-s-cooperation" placeholder="密码" v-model="form.password"></el-input>
            </el-form-item>
            <el-row class="btn-row">
                <el-button @click='login' type="primary">登录</el-button>
                <el-button @click="resetLoginForm" type="info">重置</el-button>
            </el-row>
        </el-form>
      </el-card>
  </div>
</template>

<script>
import {mapMutations} from 'vuex'
import {SET_USER_NAME} from '../store/mutation-types'
export default {
    data(){
        return{
            form:{
                name:'',
                password:''
            },
            loginRuules:{
                name: [
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                    { min: 3, max: 18, message: '长度在 3 到 18 个字符', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
                ],
            },
         
        }
    },
   
   
    methods:{
        ...mapMutations([
            SET_USER_NAME
        ]),
        //重置表单
        resetLoginForm:function(){
            this.$refs.formRef.resetFields()
        },
        //登录
        login:function(){
            this.$refs.formRef.validate((valid) => {
                console.log(valid)
                if(valid){
                    //模拟请求登录接口
                    var getLoginRequest = new Promise((resolve,reject) => {
                        this.$message({
                            showClose: true,
                            message: '登录成功',
                            type: 'success'
                        })
                        setTimeout(() => {
                            resolve('123456789')
                        },1500)
                    })
                    getLoginRequest.then(res => {
                        console.log(res)
                        localStorage.setItem('token',res)
                        console.log(this.form.name)
                        // this.SET_USER_NAME(this.form.name)
                        localStorage.setItem('username',this.form.name)
                        this.$router.push({
                            path:'/home'
                        })
                        
                    })
                }
            })
        }
    },
    mounted(){
        
    }
}
</script>

<style scoped>
    .login-wrap{
        width:100%;
        height:100%;
        background:skyblue;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .box-card{
        /* padding:20px 15px; */

    }
    .password-item{
        margin-bottom: 0;
    }
    .btn-row{
        display: flex;
        justify-content: space-around;
        margin-top: 20px;

    }
    .user-headimg{
        width:100px;
        height: 100px;
        border-radius: 50%;
        margin:0 auto;

    }
    .user-headimg img{
        width:100%;
        height:100%;
        border-radius: 50%;
    }
</style>


